
import { defineComponent, ref } from 'vue'
import { useStore } from 'vuex'
import useFetching, { FetchStatus, ResponseFetching } from '@/hooks/useFetching'
import { IFilterGroupForm } from '@/types'
import PageCard from '@/components/PageCard.vue'
import FilterGroupForm from '@/views/catalog/filter/forms/FilterGroupForm.vue'

export interface IData {
  model: IFilterGroupForm | null;
}

export default defineComponent({
  setup () {
    const store = useStore()

    const [loadForm, loadStatus, loadError] = useFetching((id: number) => {
      return store.dispatch('filterGroup/loadForm', id)
    }) as ResponseFetching<Promise<IFilterGroupForm>>

    const [saveForm, saveStatus, saveError] = useFetching((id: number, dataForm: IFilterGroupForm) => {
      return store.dispatch('filterGroup/updateForm', { id, dataForm })
    }) as ResponseFetching<Promise<IFilterGroupForm>>

    return {
      loadForm,
      loadStatus,
      loadError,
      saveForm,
      saveStatus,
      saveError,
      form: ref()
    }
  },

  components: {
    PageCard,
    FilterGroupForm
  },

  computed: {
    isLoading (): boolean {
      return this.saveStatus === FetchStatus.pending || this.loadStatus === FetchStatus.pending
    }
  },

  data (): IData {
    return {
      model: null
    }
  },

  async mounted () {
    this.model = await this.loadForm(this.$route.params.id)
  },

  methods: {
    handleBack () {
      this.$router.push({ name: 'filter' })
    },

    async handleSubmit () {
      // if (this.form && await this.form.validate()) {
      // }
      const data: IFilterGroupForm = await this.saveForm(this.$route.params.id, this.model)
      if (typeof data !== 'undefined') {
        this.model = data
      }
    }
  }
})
